<template>
    <div v-if="blog">
        <div class="mainHeader">
            <AppHeader>{{ blog.title }}</AppHeader>
            <b-button class="maincolor" type="button" to="/">back</b-button>
        </div>
        <b-card tag="article">
            <b-card-img :src="blog.Image" :alt="blog.alt" top></b-card-img>
            <b-card-text>
                {{ blog.Paragraph }}
            </b-card-text>
        </b-card>
    </div>
</template>
<script>
import axios from "axios";
import AppHeader from "../components/AppHeader";
export default {
    data: function() {
        return {
            blog: null
        };
    },
    mounted() {
        axios
            .get("http://localhost:3000/blog/" + this.$route.params.id)
            .then(res => {
                this.blog = res.data;
            });
    },
    components: {
        AppHeader
    }
};
</script>
<style lang="scss" scoped>
.mainHeader{
    position: relative;
}
.maincolor{
    border-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
}
</style>