<template>
    <div>
        <b-card :title="data.title" tag="article">
            <b-card-img :src="data.Image" :alt="data.alt" top></b-card-img>
            <b-card-text>
                {{ data.Paragraph | shorten }}
            </b-card-text>
            <b-link :to="`blog/${data.id}`">Read More</b-link>
        </b-card>
    </div>
</template>
<script>
export default {
    //props: ["Paragraph", "image", "alt", "title"],
    //props:['data'],
    props: {
        //اسمها داتا وهحط فيها معلومات عن الداتا دي زي مثلا هي ريكويرد ولا لا
        data: {
            // عشان لما يحصل ايرور نعرف هو فين بالضبط مثلا لو انا مبعتش البروب داتا دي ينبهني
            required: true
        }
    },
    filters: {
        shorten: function(v) {
            return v.substring(0, 220) + ".....";
        }
    }
};
</script>
<style lang="scss" scoped>
img {
    width: 100%;
    height: 150px;
}
</style>
