<template>
    <div>
        <AppHeader>Blog</AppHeader>
        <div v-if="blogs">
            <b-card-group>
                <!--<b-card
                    class="col-4 my-3"
                    is="blogCard"
                    v-for="blog in blogs"
                    :key="blog.id"
                    :Paragraph="blog.Paragraph"
                    :image="blog.Image"
                    :alt="blog.Image - alt"
                    :title="blog.title"
                ></b-card>تحت بعمل بيند للبلوج في البروب اللي اسمها داتا اللي في بلوج كارد
                (البلوج دي في كل لووب)
                كده بقا عندي البلوج جوه الكومبوننت في بروب اسمه داتا-->
                <b-card
                    class="col-md-4 col-12 my-3"
                    is="blogCard"
                    v-for="blog in blogs"
                    :key="blog.id"
                    :data="blog"
                >
                </b-card>
            </b-card-group>
            <center>
                <b-button
                    class="m-2 maincolor"
                    size="lg"
                    type="button"
                    @click="loadMore"
                    v-if="blogs && blogs.length < totalCount"
                >
                    <!-- يكون في بلوجز اصلا وكمان طولها اقل من عدد الداتا يعني طول م الداتا المتاحه لسه مخلصتش-->
                    More
                </b-button>
            </center>
        </div>
    </div>
</template>

<script>
import AppHeader from "../components/AppHeader";
import blogCard from "../components/blogCard";
//import jsonblogs from "./json/blog.json";
import axios from "axios";
export default {
    data: function() {
        return {
            blogs: null,
            pageNumber: 1,
            totalCount: null
        };
    },
    name: "Home",
    components: {
        AppHeader,
        blogCard
    },
    mounted() {
        this.getBlogs(this.pageNumber);
    },
    methods: {
        getBlogs: function(page) {
            //get اللينك ده هو اللي عندي بعد م بفتح الجسون سرفر وادخل ع بلوج
            axios
                .get(`http://localhost:3000/blog?_page=${page}&_limit=3`)
                .then(res => {
                    this.blogs = this.blogs
                        ? this.blogs.concat(res.data)
                        : res.data;
                    //عشان اضيف ع الموجود مش اعمل عليه override
                    //concat عشان الاتنين عباره عن arrays الجايه والموجوده
                    this.totalCount = res.headers["x-total-count"];
                });
        },
        loadMore: function() {
            this.pageNumber++;
            this.getBlogs(this.pageNumber);
        }
    }
};
</script>
